body::-webkit-scrollbar {
  width: 12px; /* Width of the scrollbar */
}

/* Track of the scrollbar */
body::-webkit-scrollbar-track {
  background: #f0f0f0; /* Background of the scrollbar track */
  border-radius: 10px; /* Rounded corners for the track */
}

/* Handle of the scrollbar */
body::-webkit-scrollbar-thumb {
  background-color: #888; /* Color of the scrollbar handle */
  border-radius: 10px; /* Rounded corners for the handle */
  border: 3px solid #f0f0f0; /* Add space around the handle */
}

/* Handle on hover */
body::-webkit-scrollbar-thumb:hover {
  background-color: #555; /* Darker color when hovered */
}

/* Handle when active (clicked) */
body::-webkit-scrollbar-thumb:active {
  background-color: #333; /* Even darker color when active */
}

/* Apply styles to the scrollbar for Firefox */
body {
  scrollbar-width: thin; /* Set the width of the scrollbar */
  scrollbar-color: #888 #f0f0f0; /* Handle and track color */
}

/* Customizing the scrollbar track and thumb for Firefox */
body::-moz-scrollbar {
  width: 12px;
}

body::-moz-scrollbar-track {
  background: #f0f0f0;
  border-radius: 10px;
}

body::-moz-scrollbar-thumb {
  background-color: #888;
  border-radius: 10px;
  border: 3px solid #f0f0f0;
}