.themain {
    overflow-y: auto; /* or auto */
    max-height: 100vh; /* or any desired height */
  }
  
  /* Width of the scrollbar */
  ::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
  .theunique{
    overflow: auto;
  }